export default {
	data() {
		return {
			theIndex: null,
			oldIndex: null,
			isStop: false,
			checkfull: false,
			finish: false,
			selectedList: [],
			sum: 0,
			items: [{
				id: 1,
				value: 228,
				price: '￥228.00',
				imgurl: '../../static/j-info6.jpg',
				number: 1,
				name: '《古文观止》全四册',
				checkone: false
			}],
			goodsList: [{
					id: 1,
					cover: '../../static/more1.jpg',
					name: '掌上游戏机300款怀旧游戏',
					price: '￥128.00'
				},
				{
					id: 2,
					cover: '../../static/more2.jpg',
					name: 'AIR FUNK 天然空气净化剂',
					price: '￥79.00'
				},
				{
					id: 3,
					cover: '../../static/more3.jpg',
					name: '自动捕螨贴 肉眼可见螨虫',
					price: '￥128.00'
				},
				{
					id: 4,
					cover: '../../static/more4.jpg',
					name: '兰味莲LAVILIN 去狐臭汗臭除味走珠',
					price: '￥179.00'
				},
				{
					id: 5,
					cover: '../../static/more5.jpg',
					name: '怡思丁 水感防嗮液 SPF50+',
					price: '￥159.00'
				},
				{
					id: 6,
					cover: '../../static/more6.jpg',
					name: 'Zendure 凯夫拉纤维快充数据线',
					price: '￥79.00'
				}
			]
		}
	},
	onLoad() {

	},
	onShow() {
		this.sum = this.items[0].value * this.items[0].number;
	},
	methods: {
		iffinish: function() {
			this.finish = !this.finish
		},
		substract: function() {
			this.items[0].number--;
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[0].checkone || this.checkfull) {
					this.sum -= this.items[i].value;
					break;
				}
			}
		},
		plus: function() {
			this.items[0].number++;
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[0].checkone || this.checkfull) {
					this.sum += this.items[i].value;
					break;
				}
			}
		},
		deleteGoods: function(id) {
			var _this=this;
			uni.showModal({
				title:'提示',
				content:'确定要删除这件宝贝吗?',
				success(res) {
					if(res.confirm){
						let len = _this.items.length;
						for (let i = 0; i < len; i++) {
							if (id == _this.items[i].id) {
								_this.items.splice(i, 1);
								break;
							}
						}
						_this.checkfull = false;
					}else if(res.cancel){
						console.log('取消');
					}
				}
			})
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150)
				}
			}
		},
		touchEnd(index, $event) {
			//结束禁止触发效果
			this.isStop = false;
		},
		//控制左滑删除效果-end
		deleteList() {
			var _this=this;
			uni.showModal({
				title: '提示',
				content: '确定要删除全部的宝贝吗?',
				success: function(res) {
					if (res.confirm) {
						let len = _this.selectedList.length;
						for (let i = 0; i < len; i++) {
							_this.deleteGoods(_this.selectedList[i]);
						}
						_this.selectedList = [];
						_this.checkfull = _this.selectedList.length == _this.items.length && _this.items.length > 0;
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		},
		toAccount(){
			uni.navigateTo({
				url:'../account/account',
				animationType:'pop-in',
				animationDuration:3000
			})
		},
		// 选中商品
		selected(index) {
			this.items[index].checkone = this.items[index].checkone ? false : true;
			let i = this.selectedList.indexOf(this.items[index].id);
			i > -1 ? this.selectedList.splice(i, 1) : this.selectedList.push(this.items[index].id);
			this.checkfull = this.selectedList.length == this.items.length;
		},
		//全选
		checkall: function() {
			this.checkfull = !this.checkfull;
			if (this.items.length == 1) {
				this.items[0].checkone = this.checkfull;
			}
		},
		allSelect() {
			let len = this.items.length;
			let arr = [];
			for (let i = 0; i < len; i++) {
				this.items[i].checkone = this.checkfull ? false : true;
				arr.push(this.items[i].id);
			}
			this.selectedList = this.checkfull ? [] : arr;
			this.checkfull = this.items.length == 0 ? false : true;
		}
	}
}
